<template>
    <div id='detail_div'>
        <div class="Header">
            <h3 id="title">{{ $route.params.sysname }}</h3>
            <img id="more" src="../assets/more.png" alt="更多设置" v-on:click='otherset' />
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
            <p>cccccccccccccccccccccccccccccccc</p>
        </div>
    </div>
</template>

<script>
import Hammer from 'hammerjs';
export default {
  data() {
    return {};
  },
  methods: {
    otherset: function() {
      alert("待开发中");
    },
    bindSwipeEvent: function() {
      var content = document.getElementById("detail_div");
      var hammer = new Hammer(content);
      hammer.get("swipe").set({ threshold: 100 });
      hammer.on("swiperight", e => {
        this.$router.back(-1);
      });
    }
  },
  mounted: function() {
      this.bindSwipeEvent();
  },
  components: {}
};
</script>

<style>
#more {
  float: right;
  width: 25px;
  height: 25px;
}
#title {
  display: inline;
  padding: 0px 0px 0px 32px;
}

.Header {
  text-align: center;
  margin-top: 0.3cm;
  margin-right: 10px;
}
</style>



